.App {
    text-align: center;
    display: flex;
    align-items: center;
    width: 1100px;
    height: 400px;
    background-color: #F8F8F8;
    flex-direction: column;
    margin-top: 50px;
    border: 2px solid;
}

.Container {
    width: 1050px;
    height: 321px;
    background-color: #ffffff;
}

.Navigation {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.SearchBar{
    display: flex;
    margin-top: 14px;
}

